
import {
  IonApp,
  IonContent,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonListHeader,
  IonMenu,
  IonMenuToggle,
  IonNote,
  IonRouterOutlet,
  IonSplitPane,
} from "@ionic/vue";
import { defineComponent, ref } from "vue";
import { useRoute } from "vue-router";
import {
  medkitOutline,
  medkitSharp,
  tvOutline,
  tvSharp,
  bagAddOutline,
  bagAddSharp,
  logInOutline,
  logInSharp,
  logOutOutline,
  logOutSharp,
} from "ionicons/icons";

export default defineComponent({
  name: "App",
  components: {
    IonApp,
    IonContent,
    IonIcon,
    IonItem,
    IonLabel,
    IonList,
    IonListHeader,
    IonMenu,
    IonMenuToggle,
    IonNote,
    IonRouterOutlet,
    IonSplitPane,
  },
  setup() {
    const selectedIndex = ref(0);
    const appPages = [
      {
        title: "Login",
        url:'/login',
        iosIcon: logInOutline,
        mdIcon: logInSharp,
      },
      {
        title: "Private Hospitals",
        url:'/channel1',
        iosIcon: medkitOutline,
        mdIcon: medkitSharp,
      },
      {
        title: "Ayurvedic Hospitals",
        url:'/channel2',
        iosIcon: medkitOutline,
        mdIcon: medkitSharp,
      },
      {
        title: "Vedio Consultation",

        iosIcon: tvOutline,
        mdIcon: tvSharp,
      },
      {
        title: "Medic Lab",

        iosIcon: bagAddOutline,
        mdIcon: bagAddSharp,
      },
      {
        title: "Log Out",
        url: "/login",
        iosIcon: logOutOutline,
        mdIcon: logOutSharp,
      },
    ];
    const labels = [
      "Family",
      "Friends",
      "Notes",
      "Work",
      "Travel",
      "Reminders",
    ];

    const path = window.location.pathname.split("folder/")[1];
    if (path !== undefined) {
      selectedIndex.value = appPages.findIndex(
        (page) => page.title.toLowerCase() === path.toLowerCase()
      );
    }

    const route = useRoute();

    return {
      selectedIndex,
      appPages,
      labels,
      medkitOutline,
      medkitSharp,
      tvOutline,
      tvSharp,
      bagAddOutline,
      bagAddSharp,
      logInOutline,
      logInSharp,
      logOutOutline,
      logOutSharp,
      isSelected: (url: string) => (url === route.path ? "selected" : ""),
    };
  },
});
